<template>
    <div class="t-container" v-bind:style="{ 'height':+pContainerHeight +'px'}">
        <b-row align-h="between" no-gutters>
            <b-col>
                <b-img src="/assets/badge_resize.png" fluid alt="Responsive image"></b-img>
            </b-col>
            <b-col>
                <b-img src="/assets/ongoing.png" fluid alt="Responsive image"></b-img>
            </b-col>
            <b-col>
                <b-img src="/assets/done.png" fluid alt="Responsive image"></b-img>
            </b-col>
        </b-row>
        <div class="p-container">
            <div>
                <h4>已執行專案</h4>
                <a>自己曾捐贈且已經募資完畢，正被執行的專案</a>
            </div>
            <vc-donut background="white" foreground="grey"
            :size='pieChartSize' unit="px" :thickness="30"
            has-legend legend-placement="bottom"
            :sections="sections" :total="100"
            :start-angle="0"
            @section-click="handleSectionClick">總專案數<br/>{{sections.length}}</vc-donut>
        </div>
    </div>
</template>

<script>
export default {
    name: 'App',
    components: {
    },
    data() {
      return {
        isProjectListShow: false,
        isVisible: false,
        projectList:[
            "伊甸基金會-專案1-捐贈100p",
            "兒福聯盟-專案1-捐贈200p",
        ],
        currentTab: "",
        pContainerHeight: 500,
        pieChartSize:200,
        sections: [
          { label: '伊甸基金會', value: 50, color: '#FF6384' },
          { label: '兒福聯盟', value: 20, color: '#36A2EB' },
          { label: '熊大專案', value: 30, color: '#FFCE56' }
        ]
      }
    },
    mounted() {
        this.pContainerHeight = Math.ceil((window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight) - 80)
        this.pieChartSize = Math.ceil((window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth) * 0.55)
        console.log(window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight)
        console.log(this.pContainerHeight)
    },
    computed: {

    },
    methods: {
      updatePieData() {
        this.$refs.pieChart.chart.data([
          ['Product A', 4755],
          ['Product B', 5205],
          ['Product C', 1504]
        ]);
        this.pieDataIsModified = true;
      },
      showProjects(){
          console.log("button click")
          this.isVisible = !this.isVisible
          
      },
      handleSectionClick(section) {
        console.log(`${section.label} clicked.`);
      }
    }
};
</script>

<style>
.center {
  margin: auto;
  width: 35%;
  /* padding: 10px; */
}
.chart {
    width: 100%;
    height: 400px;
    margin-bottom: 10px;
}
.chart-container {
    text-align: center;
    margin-top: 75px;
    margin-bottom: 15px;
}
.chart-list .chart-container:first-child {
    margin-top: 35px;
}
.nav-tabs > li > a {
    padding-top: 10%; 
    padding-bottom: 10%;
}

.t-container{
    height:100%
}

.tabs{
    height: 100%
}

.tab-content {
    height:90%
}

.t-container {
    height: 500px;
    background-color: white;
}

.p-container {
    text-align: center;
    /* padding-top: px; */
}

.p-container > div{
    padding-top: 20px;
}

</style>


